import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSelectChange } from '@angular/material/select';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ExitDirtyComponent } from 'src/app/shared/modals/exit-dirty/exit-dirty.component';
import { reportModel, reportStatus } from 'src/app/shared/models/report.model';
import { CriteriaService } from 'src/app/shared/Services/criteria.service';
import { ReportService } from 'src/app/shared/Services/report.service';
@Component({
  selector: 'app-report-configuration',
  templateUrl: './report-configuration.component.html',
  styleUrls: ['./report-configuration.component.scss']
})
export class ReportConfigurationComponent implements OnInit {
  report: reportModel;
  reports: reportModel[];
  form:FormGroup;
  payLoad:any;

  constructor(private cs: CriteriaService
    , private rs: ReportService
    , private route: ActivatedRoute
    , private _snackBar: MatSnackBar
    , private fb: FormBuilder
    , private router: Router
    , public dialog: MatDialog) {
      this.rs!.getReports().subscribe(data => {
        this.reports = data;
      });

      route.params.subscribe(val => {
        this.rs!.getReport(this.route.snapshot.paramMap.get('id'), true).subscribe(data => {
          this.report = data;
          this.buildForm();
        },(jsonData) => { }, () => { }); 
      });
  }

  openDialog(url:string) {
    const dialogRef = this.dialog.open(ExitDirtyComponent);

    dialogRef.afterClosed().subscribe(result => {
      if(result == true) this.router.navigate([url]);
    });
  }

  ngAfterViewInit() {
   
  }

  navigate(event: Event) {

    let value: string = (event as unknown as MatSelectChange).value; 
    if(value == undefined) return;
    let _url = `/ReportConfiguration/${value}`;
  if(this.form.dirty){
    this.openDialog(_url);
  } else {
    this.router.navigate([_url]);
  }
  }
  
  ngOnInit(): void {

  }

  buildForm(){
    var criterias = this.report.criterias.filter(s => !s.hidden).sort((a,b) => a.order - b.order);
    const criteriaGroup = criterias.map(x => this.fb.group({
      timestamp : this.fb.control(x.timestamp),
      id: this.fb.control(x.id),
      label: this.fb.control(x.label, Validators.required),
      name: this.fb.control(x.name),
      controlType: this.fb.control(x.controlType, Validators.required),
      referential: this.fb.control(x.referential),
      multiple: this.fb.control(x.multiple),
      order: this.fb.control(x.order),
      required: this.fb.control(x.required),
      reportID: this.fb.control(x.reportID),
      dataType: this.fb.control(x.dataType)
    }));

    const subreportGroup = this.report.subreports.map(x => this.fb.group({
      label: this.fb.control(x.label, Validators.required),
      name: this.fb.control(x.name),
      parentReportID: this.fb.control(x.parentReportID),
      id: this.fb.control(x.id),
      categoryID : this.fb.control(x.categoryID),
      timestamp : this.fb.control(x.timestamp),
      reportPath: new FormControl(x.reportPath),
      reportServer: new FormControl(x.reportServer),
      subreport: new FormControl(x.subreport)
    }));
    
    this.form = new FormGroup({
      timestamp : this.fb.control(this.report.timestamp),
      label : new FormControl(this.report.label, Validators.required),
      name : new FormControl({value:this.report.name, disabled: true}),
      category : new FormControl({value:this.report.category.name, disabled: true}),
      updated : new FormControl({value:this.report.updatedOn, disabled: true}),
      reportPath: new FormControl(this.report.reportPath, Validators.required),
      reportServer: new FormControl(this.report.reportServer, Validators.required),
      subreports: this.fb.array(subreportGroup),
      criterias: new FormArray(criteriaGroup)
    });
  }

   validator(ctrl) :boolean {
    const validator = this.form.get(ctrl).validator({} as AbstractControl);
    if (validator && validator.required) {
      return true;
    }
    return false;
  }

  onSubmit() {
    Object.assign(this.report, this.form.value);
    this.report.status = reportStatus.Supported;
    this.report.updatedOn = new Date();

    this.rs.updateReport(this.report).subscribe(respons => {
      this.report = respons as unknown as reportModel;
      this._snackBar.open('Successfully saved !', '', { duration: 6000, panelClass: ['alert-success'] });
    });
  }
}
