import { criteriaModel } from './criteria.model';

export interface reportModel {
    order: number;
    parentReportID: string;
    reportServer: string;
    categoryID: string;
    reportPath:string;
    name: string;
    label: string;
    id: string;
    updatedOn: Date;
    updatedBy: string;
    scheduled: boolean;
    status: number;
    criterias: criteriaModel[];
    category: categoryModel;
    subreports: reportModel[];
    timestamp: string;
    subreport: boolean;
}

export interface categoryModel {
    id: string;
    name: string;
    label: string;
}

export enum reportStatus {
    'Unsupported' = 0,
    'Supported' = 1,
    'Inactive' = 2
}