import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { autoCompleteModel } from '../models/Referential.Model';
import { option } from '../Util/criteria-base';
import { HttpHelperService } from './http-helper.service';


@Injectable({
  providedIn: 'root'
})
export class ReferentialService {

  constructor(private httpHelper: HttpHelperService) { }

  getOptions(model: autoCompleteModel): Observable<option[]> {
    return this.httpHelper.get('/referential/options', {params: model});
  }
}
