import { CategoryConfigurationComponent } from './category-configuration/category-configuration.component';
import { GlobalSettingComponent } from './global-setting/global-setting.component';
import { ReportConfigurationComponent } from './report-configuration/report-configuration.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {path: 'GlobalConfiguration', component:GlobalSettingComponent },
  {path: 'CategoryConfiguration', component:CategoryConfigurationComponent },
  { path: 'ReportConfiguration', component: ReportConfigurationComponent },
  { path: 'ReportConfiguration/:id', component: ReportConfigurationComponent }

];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SettingRoutingModule { }


